section#home {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  padding: 100px 20px 0;
  background-image: url("/img/header.jpg");
  background-size: cover;
  background-repeat: no-repeat;

  div.content {
    max-width: $siteWidth;
    margin: 0 auto;
    color: #fff;
  }
}
